.featured {
    height: 100vh;
    
}

.featured--vertical {
    width: inherit;
    height: inherit;
    background: linear-gradient(to top, #111 10%, transparent 90%);
}

.featured--horizontal {
    width: inherit;
    height: inherit;
    background: linear-gradient(to right, #111 30%, transparent 70%);
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-left: 2rem;
    padding-bottom: 250px;
}

.featured--name {
    font-size: 3.75rem;
    font-weight: bold;
    max-width: 60%;
}

.featured--info {
    font-size: 1.125rem;
    font-weight: bold;
    margin-top: 1rem;
}

.featured--info--green {
    color: #46d369;
}

.featured--info-item {
    display: inline-block;
    margin-right: 1rem;
}

.featured--description {
    margin-top: 1rem;
    font-size: 1.25rem;
    color: #999;
    max-width: 40%;
}

.featured--buttons {
    margin-top: 1rem;
}

.featured--buttons a {
    display: inline-block;
    font-size: 1.25rem;
    font-weight: bold;
    padding: 0.8rem 1.3rem;
    border-radius: 5px;
    text-decoration: none;
    margin-right: 10px;
    opacity: 1;
    transition: all ease 0.2s;
}

.featured--buttons a:hover {
    opacity: 0.7;
}

.featured--button--dark {
    background-color: #333;
    color: #fff;
}

.featured--button--light {
    background-color: #fff;
    color: #000;
}

.featured--gender {
    margin-top: 1rem;
    font-size: 1.125rem;
    color: #999;
}

@media (max-width: 760px) {
    .featured{
        height: 90vh;
    }
    .featured--name{
        font-size:40px;
    }
    .featured--info{
        font-size:16px;        
    }
    .featured--description{
        font-size:14px; 
        max-width: 100%;
        margin-right: 30px;    
    }
    .featured--mylistbutton,
    .featured--watchbutton{
        font-size:10px; 
    }
    .featured--genres{
        font-size:14px;        
    }
}