@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
* {
  box-sizing: border-box;
}

body {
  background-color: #111;
  color: #fff;
  font-family: 'Roboto', sans-serif;
  margin: 0;
}

.lista {
  margin-top: -250px;
}

footer {
  margin: 50px 0;
  text-align: center;
}

.loading{
  position: fixed;
  top:0;
  left:0;
  right: 0;
  bottom: 0;
  z-index: 99;
  background-color: #000;
  display:-webkit-flex;
  display:flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}
.movierow {
    margin-bottom: 2rem;
}

.movierow h2 {
    margin: 0 0 0 2rem;
}

/* Area que define as caracteriscas da lista de filmes */
.movierow--listarea {
    overflow-x: hidden;
    padding-left: 2rem;
}

.movierow--item {
    cursor: pointer;
    display: inline-block;
    width: 150px;
}

.movierow--item img {
    width: 100%;
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
    transition: all ease 0.2s;
}

/* Cria efeito de zoom na imagem quando passamos o mouse por cima */
.movierow--item img:hover {
    -webkit-transform: scale(1);
            transform: scale(1);
}

.movierow--buttons {
    position: absolute;
    width: 40px;
    height: 225px;
    background-color: rgba(0,0,0,0.6);
    z-index: 99;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    overflow-x: hidden;
    cursor: pointer;
    opacity: 0;
    transition: all ease .5s;
}

.left {
    left: 0;
}

.right {
    right: 0;
}

.movierow:hover .movierow--buttons{
    opacity: 1;
}

@media (max-width: 760px) {
    .movierow--buttons{
        opacity: 1;
    }
}
.featured {
    height: 100vh;
    
}

.featured--vertical {
    width: inherit;
    height: inherit;
    background: linear-gradient(to top, #111 10%, transparent 90%);
}

.featured--horizontal {
    width: inherit;
    height: inherit;
    background: linear-gradient(to right, #111 30%, transparent 70%);
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    padding-left: 2rem;
    padding-bottom: 250px;
}

.featured--name {
    font-size: 3.75rem;
    font-weight: bold;
    max-width: 60%;
}

.featured--info {
    font-size: 1.125rem;
    font-weight: bold;
    margin-top: 1rem;
}

.featured--info--green {
    color: #46d369;
}

.featured--info-item {
    display: inline-block;
    margin-right: 1rem;
}

.featured--description {
    margin-top: 1rem;
    font-size: 1.25rem;
    color: #999;
    max-width: 40%;
}

.featured--buttons {
    margin-top: 1rem;
}

.featured--buttons a {
    display: inline-block;
    font-size: 1.25rem;
    font-weight: bold;
    padding: 0.8rem 1.3rem;
    border-radius: 5px;
    text-decoration: none;
    margin-right: 10px;
    opacity: 1;
    transition: all ease 0.2s;
}

.featured--buttons a:hover {
    opacity: 0.7;
}

.featured--button--dark {
    background-color: #333;
    color: #fff;
}

.featured--button--light {
    background-color: #fff;
    color: #000;
}

.featured--gender {
    margin-top: 1rem;
    font-size: 1.125rem;
    color: #999;
}

@media (max-width: 760px) {
    .featured{
        height: 90vh;
    }
    .featured--name{
        font-size:40px;
    }
    .featured--info{
        font-size:16px;        
    }
    .featured--description{
        font-size:14px; 
        max-width: 100%;
        margin-right: 30px;    
    }
    .featured--mylistbutton,
    .featured--watchbutton{
        font-size:10px; 
    }
    .featured--genres{
        font-size:14px;        
    }
}
header {
    position: fixed;
    z-index: 9999;
    top: 0;
    left: 0;
    right: 0;
    height: 70px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    padding: 0 2rem;
    background: transparent;
    transition: all ease 0.3s;
    
}

.black {
    background-color: #141414;
}

.header--logo svg{
    cursor: pointer;
    height: 30px;
}

.header--user img{
    cursor: pointer;
    height: 35px;
}
